*{
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 50px;
}

input{
  width: 100%;
  font-size: 1.2rem;
  background-color: whitesmoke;
  color:darkcyan;
  border-color: rgb(64, 87, 68);
}


.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}


.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}